// Case study

body[data-controller="case-study"] {

  p.summary {
    margin: 0 2.6rem 3rem;
  }

  section.devices {
    margin-bottom: 2rem;
    overflow-x: hidden;

    img {
      width: 100%;
    }
  }

  section.screens figure[role="group"]:only-child figcaption {
    display: none;
  }

  section.Linode {
    figure[role="group"].callout {
      figure {
        margin-bottom: 1rem;
      }
    }
  }

  section.hibu {
    figure.callout.concept,
    figure.callout.prototype {
      section {
        display: flex;
        justify-content: space-between;

        figure:nth-child(1) {
          flex: 0 0 35.3%;
          margin: 0;
        }
        figure:nth-child(2) {
          flex: 0 0 60%;
          margin: 0;
        }
      }
    }
  }

  section.Fitly {
    figure.callout.pricing-cta {
      figure:nth-child(1) {
        margin-top: 0;
        margin-bottom: 1rem;
      }
      figure:nth-child(2) {
        margin: 0 auto 2rem;
      }
    }
  }

  // No borders around mobile screens
  section.Vidyo {
    section.screens figure[role="group"] {
      &:nth-child(4) {
        figure {
          border: none;
        }
      }
    }
  }

  section.screens.group {
    margin-bottom: 4rem;
    padding-top: 2rem;
    background: hsl(175,55%,76%);
    
    figure[role="group"] figure {
      border: 0.1rem solid hsl(175,55%,66%);
    }
  }

  @media only screen and (min-width: 768px) {
    main h1 span.title {
      display: initial;
    }

    p.summary {
      margin-right: 4.8rem;
      margin-left: 4.8rem;
    }

    section.devices {
      width: auto;
      margin-bottom: 4rem;
      margin-left: 0;
      padding: 0 4rem;
    }

    section.Linode,
    section.Vidyo,
    section.hibu,
    section.Fitly {
      section.devices img {
        margin: 0 !important;
        width: 100% !important;
      }
    }

    section.Abercrombie-and-Fitch,
    section.OncoTracker {
      section.devices {
        display: flex;
        justify-content: center;

        img {
          width: auto;
          height: 27rem;
        }
      }
    }

    section.Linode {
      figure[role="group"]:not(.screens) {
        section {
          display: flex;
          justify-content: space-between;

          figure {
            img {
              height: 23rem;
            }
          }
        }

        &.usability-testing {
          img {
            height: 12rem;
          }
        }
      }

      figure[role="group"].callout {
        figure {
          margin-bottom: 0;
        }
      }
    }

    figure.callout {
      // Fitly
      &.zip-code {
        img {
          height: 30rem;
        }
      }

      &.kitchen-view {
        img {
          height: 48rem;
        }
      }

      &.pricing-cta {
        figure {
          margin-bottom: 2rem;
        }

        img {
          height: 24rem;
        }
      }
    }

    section.hibu {
      figure.callout.concept,
      figure.callout.prototype {
        section {
          justify-content: center;

          figure:nth-child(1),
          figure:nth-child(2) {
            flex-basis: auto;
            margin: 0 1rem;
          }
        }
      }
    }

    main nav {
      position: initial;
      justify-content: center;
      margin: 6rem 0;
    }
  }

  @media only screen and (min-width: 840px) {
    section.screens.group {
      padding-bottom: 2rem;

      figure[role="group"] {
        figure {
          margin-bottom: 0;
        }
      }
    }
  }

  @media only screen and (min-width: 1024px) {
    section.about {
      display: grid;
      grid-template: auto/37% 63%;
      max-width: 120rem;
      margin: 8rem auto 6rem;
    }

    p.summary {
      grid-column: 1;
      margin-bottom: 0;
      font-size: 1.8rem;
      text-align: left;
    }

    section.devices {
      grid-row: 1/4;
      grid-column: 2;
      margin: 2rem 0 0 4rem;
      padding: 0;
    }

    section.screens.group {
      padding-top: 2rem;
    }

    section.Linode {
      figure[role="group"]:not(.screens) {
        section {
          figure {
            img {
              height: 32rem;
            }
          }
        }

        &.usability-testing {
          img {
            height: 17rem;
          }
        }
      }
    }
  }

  @media only screen and (min-width: 1280px) {
    section.screens.group {
      padding: 0;

      figure[role="group"] {
        section {
          padding: 3rem;
        }
        figure {
          flex-basis: 28rem;
          margin: 0 3rem 0 0;
        }
      }
    }
  }
}
